// Variables
$article-height: auto;
$article-width: 60%;
$article-width-alt: 87.5%;

// Styles

.adt-news {
  @include flex(row, center, center);

  > .adt-article {
    @include constrain($article-width);
    @include respond-to($breakpoint-schmedium) {
      @include constrain($article-width-alt);

      max-width: $article-width-alt; }

    max-width: $article-width; } }

