// Variables
$deck-height: 100%;
$deck-width: 100%;

.ado-deck {
  @include flex(column, center, space-between);

  @include respond-to($breakpoint-schmedium, min) {
    height: 92.5%; }

  @include size($deck-width, $deck-height);

  background: {
    position: center center;
    repeat: no-repeat;
    size: cover; } }

