//// ---------------------------------------------------------------------------
/// This file contains all application-wide Sass placeholders.
//// ---------------------------------------------------------------------------

/// Makes an element background color Diamondback Blue
/// @requires {mixin} set-bkg-color-and-hover
%ui-accent-dark {
  @include set-bkg-color-and-hover($accent-dark); }

/// Makes an element background color Diamondback Red
/// @requires {mixin} set-bkg-color-and-hover
%ui-accent-med {
  @include set-bkg-color-and-hover($accent-med); }

/// Makes an element background color Diamondback Yellow
/// @requires {mixin} set-bkg-color-and-hover
%ui-accent-light {
  @include set-bkg-color-and-hover($accent-light); }

/// Removes the border from an element
/// @requires {mixin} borderless
%ui-borderless {
  @include borderless; }

/// Make an element full screen
/// @requires {mixin} size
%ui-full {
  @include size(100vw, 100vh); }

/// Make a element not transparent
/// @requires {mixin} opacity
%ui-not-transparent {
  @include opacity(1); }

/// Make an element the height of its parent
/// @requires {mixin} stretch
%ui-stretch {
  @include stretch; }

/// Make an element transparent
/// @requires {mixin} opacity
%ui-transparent {
  @include opacity(0);

  background-color: transparent; }
