// Style

.adt-articles {
  @include flex(column, center, center);

  > .ada-container {
    @include flex(row, flex-start, space-between);

    height: 100%; } }

.articles {
  > .adt-article {
    margin-bottom: size(40);

    .article-header > .ada-paragraph {
      @include respond-to($breakpoint-xl) {
        font-size: size(16); } } } }

.left-rail,
.right-rail {
  height: 100%; }

.left-rail {
  @include constrain(42.5%);
  @include flex(column, flex-start, center); }

.right-rail {
  @include constrain(max-content);
  @include flex(row, center, center);

  padding-right: size(16 * 4);

  > .ada-advertisement {
    @include size(size(300), size(600)); } }
