// -----------------------------------------------------------------------------
// This file contains all application-wide animations.
// -----------------------------------------------------------------------------

// Makes an element increase its opacity over time
.ui-reveal {
  @include keyframes(ui-reveal) {
    0% {
      @include opacity(0); }

    25% {
      @include opacity(0.25); }

    50% {
      @include opacity(0.5); }

    75% {
      @include opacity(0.75); }

    100% {
      @include opacity(1); } } }

// Ticker effect
.ui-ticker {
  @include keyframes(ui-ticker) {
    0% {
      transform: translate3d(0, 0, 0);
      visibility: visible; }

    50% {
      transform: translate3d(-50%, 0, 0); }

    100% {
      transform: translate3d(-100%, 0, 0); } } }

// Makes an element decrease its opacity over time
.ui-vanish {
  @include keyframes(ui-vanish) {
    0% {
      @include opacity(1); }

    25% {
      @include opacity(0.75); }

    50% {
      @include opacity(0.5); }

    75% {
      @include opacity(0.25); }

    100% {
      @include opacity(0); } } }
