// Default heading text
// 1. Set font weight
// 2. Remove margin
.ada-heading {
  color: $light;
  font-weight: 700;
  margin: 0; }

h1.ada-heading {
  @include respond-to($breakpoint-schmedium) {
    font-size: size(30); }


  font-size: size(40); }
