// Styles

// Stack content
// Add breathing room to DBKNews online plug
// Set image width and max-width
.adm-logo {
  @include flex(column, center, center);

  height: min-content;

  > .ada-heading {
    margin-top: size(16); }

  > .ada-image {
    @include constrain(100%);
    max-width: 100%;
    width: 100%; } }
