// Styles

// Position inner content in the center
.ads-loading {
  > .ada-container {
    @include flex(row, center, center);

    .adm-logo {
      @include respond-to($breakpoint-xxl, min) {
        width: size(800); }


      @include respond-to($breakpoint-xxxl, min) {
        font-size: size(1000); }

      width: size(600); } } }
