// ----------------------------------------------------------------------------
// Paragraph specific styles, variables, placeholders, mixins and functions
// @see components/atoms/Paragraph.jsx
// @author Lexus Drumgold
// ----------------------------------------------------------------------------

// Styles

.ada-paragraph {
  color: $light;
  margin: 0;

  > span {
    font-weight: 600; } }

.author,
.blurb {
  color: $light; }

.author,
.category {
  @include respond-to($breakpoint-xxl, min) {
    font-size: size(20); }


  @include respond-to($breakpoint-xxxl, min) {
    font-size: size(26.667); }


  font-size: size(15);
  text-transform: uppercase; }

// ! Showdown creates multiple paragraphs
.blurb,
.blurb > p {
  @include respond-to($breakpoint-xxl, min) {
    font-size: size(22); }

  @include respond-to($breakpoint-xxxl, min) {
    font-size: size(29.333); }

  font-size: size(16.5);
  margin: size(10) 0 size(20);
  width: 100%; }

.category {
  color: $accent-med;
  font-weight: 600;
  margin-right: size(10); }

.subheading {
  @include respond-to($breakpoint-xxl, min) {
    font-size: size(24); }

  @include respond-to($breakpoint-xxxl, min) {
    font-size: size(32); }

  font: {
    size: size(18) !important;
    weight: 300; }
  margin-bottom: size(10);
  text-transform: uppercase;

  > .ada-link {
    color: $light;
    font-weight: 500; } }
