/// Variables

/// Button component font sizes
/// @type List
$button-font-sizes: ('small': 14, 'regular': 16, 'large': 18);

// Default button
// 1. Style &:hover, &:active, &:focus -> add cursor and increase font weight
// 2. Center button elements side by side
// 3. Target disabled buttons
.ada-button {
  @include event {
    cursor: pointer;
    font-weight: 600; }


  @include flex(row, center, center);
  @include respond-to($breakpoint-xxl, min) {
    font-size: size(map-get($button-font-sizes, large)); }


  @include respond-to($breakpoint-schmedium) {
    font-size: size(map-get($button-font-sizes, small)); }


  font-size: size(map-get($button-font-sizes, regular));

  &:disabled {
    @include set-bkg-color-and-hover($light-gray); }

  // Background color = Diamondback Blue
  &.ui-accent-dark {
    @include set-bkg-color-and-hover($accent-dark); }

  // Background color = Diamondback Red
  &.ui-accent-med {
    @include set-bkg-color-and-hover($accent-med); }

  // Background color = Diamondback Yellow
  &.ui-accent-light {
    @include set-bkg-color-and-hover($accent-light); }

  // Remove default border
  &.ui-borderless {
    border: none; }

  // Background color = Dark base color
  &.ui-dark {
    @include set-bkg-color-and-hover($dark); }

  // Background color = Light base color
  &.ui-light {
    @include set-bkg-color-and-hover($dark); }

  // Background color = transparent
  &.ui-transparent {
    background-color: transparent; }

  // Background color = white
  &.ui-white {
    @include set-bkg-color-and-hover($white); } }
