// Default link
// 1. Style &:hover, &:active, &:focus -> add cursor and increase font weight
// 2. Set default color to Diamondback Red
// 3. Remove text decoration
.ada-link {
  @include event {
    cursor: pointer;
    font-weight: 600; }


  color: $accent-med;
  text-decoration: none; }

.headline {
  @include event {
    font-weight: 700; }

  @include respond-to($breakpoint-xxl, min) {
    font-size: size(30); }

  @include respond-to($breakpoint-xxxl, min) {
    font-size: size(40); }

  color: $light;
  font: {
    size: size(22.5);
    weight: 700; }

  &.feature {
    @include respond-to($breakpoint-xxl, min) {
      font-size: size(48); }

    @include respond-to($breakpoint-xxxl, min) {
      font-size: size(64); }

    font-size: size(36); } }

.related-article {
  @include respond-to($breakpoint-xxl, min) {
    font-size: size(20); }

  @include respond-to($breakpoint-xxxl, min) {
    font-size: size(26.667); }

  color: $light-gray;
  font: {
    size: size(15);
    style: italic;
    weight: 500; } }
